import uniIcons from "@/components/uni-icons/uni-icons.vue"
import mySwiper from '../../components/myswiper.vue'
export default {
	data() {
		return {
			imgDetails:"",
			album:[],
			comment:[],
			hot:[],
			index:0
		}
	},
	filters:{
		totime(val) {
			let date = new Date(val);
			let month = date.getMonth()+1;
			let day = date.getDate();
			return  day +"日 / " + month +"月";
		}
	},
	methods: {
		getimgdetails() {
			let {list} = getApp().globalData;
			this.imgDetails = list[this.index];
			uni.showLoading({
				title:"加载中",
				mask:true
			})
			uni.request({
				url:"/image/v2/wallpaper/wallpaper/"+ this.imgDetails.id +"/comment",
				success: (result) => {
					//console.log(result.data.res);
					this.album = result.data.res.album;
					this.comment = result.data.res.comment;
					this.hot = result.data.res.hot;
				},
				complete: () => {
					uni.hideLoading();
				}
			})
		},
		handerswierAction(e) {
			//console.log(e);
			let {list} = getApp().globalData;
			if(e.direction == 'left' && this.index < (list.length-1) ){
				this.index++;
				//this.imgDetails = list[this.index];
				this.getimgdetails();
			}else if(e.direction == 'right' && this.index >0 ){
				this.index--;
				//this.imgDetails = list[this.index];
				this.getimgdetails();
			}else{
				uni.showToast({
					title:"没有数据了",
					icon:"none"
				})
			}
		},
	    downpicture() {
			uni.showLoading({
				title:"下载中"
			})
			uni.downloadFile({
				url: this.imgDetails.thumb,
				success:(result) => {
					//console.log(result.tempFilePath)
					uni.saveImageToPhotosAlbum({
						filePath:result.tempFilePath,
						success: () => {
							uni.showToast({
								title:"保存成功"
							})
						},
						complete: () => {
							uni.hideLoading()
						}
					})
				}
			})
		}
	},
	onLoad(options) {
		let { index} = getApp().globalData;
		this.index = index;	
		this.getimgdetails();
	},
	components: {
		uniIcons,
	    mySwiper
	}
}
